<div class="form-container">
<form class="form-signin">
    <h1 class="headline-primary">Beta Sign Up</h1>
    <label for="email" class="">Email address</label>
    <input type="email" id="email" class="" placeholder="Email address" required>
    <label for="password" class="">Password</label>
    <input type="password" id="password" class="" placeholder="Password" required>
    <label for="name" class="">Name</label>
    <input type="text" id="name" class="" placeholder="Name" required>
    <button class="button-signin">Jack in</button>
    <a href="/index.html">Or go to login..</a>
  </form>
</div>

<style type="text/scss">.headline-primary {
  padding: 1.5em .75em 1em .75em; }

.form-signin {
  display: flex;
  flex-direction: column;
  max-width: 90%;
  margin: 0 auto 0 auto; }
  @media only screen and (min-width: 800px) {
    .form-signin {
      max-width: 700px; } }
  .form-signin input {
    background: #c9c9f8;
    border: none;
    margin-bottom: 1.5em;
    color: black;
    transition: all .4s  ease-in; }
    .form-signin input:focus {
      outline: none;
      transform: scale3d(1.07, 1.07, 1.07);
      box-shadow: 0 0 4px 4px blue;
      background: white; }
  .form-signin label {
    font-size: 1.35em;
    padding-bottom: .5em; }
  .form-signin .button-signin {
    background: green;
    border: black;
    font-size: 1.45em;
    color: black;
    transition: all .3s ease-in;
    margin-bottom: 2em; }
    .form-signin .button-signin:focus {
      outline: none;
      transform: scale3d(1.07, 1.07, 1.07);
      box-shadow: 0 0 4px 4px blue; }
  .form-signin a {
    font-size: 1.3em;
    transition: all .3s ease-in; }
